import { Component, AfterViewChecked, ElementRef, ViewChild, OnInit } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { CookieService } from 'angular2-cookie/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  items: FirebaseListObservable<any[]>;
  users: any;
  uid: string;
  message: any;
  auth_user: any
  selected_user: any;
  database: any;

  @ViewChild('scrollMe') private myScrollContainer: ElementRef;

  constructor(public db: AngularFireDatabase, public storage: CookieService) { }

  ngOnInit() {
    try {
      this.selected_user = "";
      this.uid = this.storage.get('uid');
      this.database = this.db.list('users/');

      this.database.forEach(element => {

          for (var i = 0; i < element.length; i++) {
            if (element[i].uid == this.uid) {
              this.auth_user = element[i]
              element.splice(i,1);
            }
          }
  
        this.users = element
      });

      this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
    } catch (err) { }
  }

  getChat() {
    this.items = this.db.list('https://real-chat-cb118.firebaseio.com/users/' + this.uid + '/inbox/' + this.selected_user);
  }

  send(uid) {
    console.log(this.auth_user)
    var message = {
      message: this.message,
      displayName: this.auth_user.Name,
      email: this.auth_user.Email,
      timestamp: Date.now()
    };
    this.db.database.ref('users/' + this.uid + '/inbox/' + this.selected_user).push(message);
    this.db.database.ref('users/' + this.selected_user + '/inbox/' + this.uid).push(message);
    this.message = '';
    try {
      this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
    } catch (err) { }
  }

}



