import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import {Router} from "@angular/router";
import {CookieService} from 'angular2-cookie/core';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  public email;
  public password;
  public database;

  constructor(public auth:AngularFireAuth, public router:Router,public db: AngularFireDatabase, public storage:CookieService) {
    this.database = this.db.list('https://real-chat-cb118.firebaseio.com/users');
   }

  ngOnInit() {
  }

  /* build app on git 
  ng build --prod --base-href "https://USERNAME.github.io/REPOSITORY_NAME/"
  https://github.com/angular-buch/angular-cli-ghpages
  */

  login(){
    console.log(this.password)
    this.auth.auth.signInWithEmailAndPassword(this.email,this.password).then((data)=>{
      console.log(data)
      
      if(data['uid'] && data['refreshToken']){
        this.storage.put('uid',data['uid']);      
        this.router.navigateByUrl('home');
      }
    }).catch(Error => console.log(Error))
  }

}



